<template>
	<div>
		<ymt-coupon-radios></ymt-coupon-radios>

		<ymt-popup :popupParam='this.popupData' v-if="popupshow" @confirmFn="handelConfirm" @cancelFn="handelCancel">
		</ymt-popup>

		<!--<div class="transition-test">
			<transition name="mytran">
				<div v-if="tranTest">
					transition test
					<p style="background-color: #000; color: #FFFFFF;">inner test</p>
				</div>
				
			</transition>
			<button @click="transitionActive">transition</button>
		</div>-->

		<div class="des">
			<h3 @click="showPop">组件说明</h3>
			<p>
				<pre>
  组件名称：ymtRadio

  样式说明：
  1.ymt-radio

  事件绑定：
  @itemChoose="选项改变事件", 在回调函数中
  可获取到选择的是哪种支付方式

  属性说明：
  initialOption：初始选项,
  如果不写该属性默认微信为支付方式

  常见用法：
  {{rawHtml}}
			  	</pre>
			</p>
		</div>

		<div style="height: 10000px;"></div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				rawHtml: `<ymt-payment-radio @itemChoose="handelChange"></ymt-payment-radio>`,
				popupshow: false,
				popupData: {},
				tranTest: true,
			}
		},
		methods: {
			handelChange(value) {
				console.log('选项为：', value);
			},
			showPop() {
				this.popupshow = true;
				this.popupData = {
					title: '弹窗标题',
					content: '弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容',
					cancelText: '取消文字',
					confirmText: '确认文字',
					popupType: 'question',
					telNum: 13036775972,
				}
			},
			handelCancel() {
				console.log("取消事件");
				this.popupshow = false;
			},
			handelConfirm() {
				console.log("确认事件");
				this.popupData = {
					title: '弹窗标题111',
					content: '哈哈进来加哦评价',
					cancelText: '取消',
					confirmText: '确认',
					popupType: 'success',
					//					telNum : 13036775972,
				}
				var _this = this;
				this.handelConfirm = function() {
					_this.popupshow = false;
				}
			},
			transitionActive() {
				this.tranTest = !this.tranTest;
				console.log(this.tranTest)
			}
		}
	}
</script>

<style lang="less" scoped>
	.des {
		margin: 1rem 0 0 0;
		font-size: 1.5rem;
	}
	
	pre {
		/*width: 100%;*/
		overflow: auto;
	}
	
	.transition-test {
		text-align: center;
		font-size: 36/20rem;
		button {
			font-size: 36/20rem;
		}
		
		.mytran-enter-active, .mytran-leave-active {
			transition: all 0.5s
		  /*background-color: red;*/
		}
		.mytran-enter, .mytran-leave-active {
			transform: scale(0,0);
			transform-origin: 50% 50%;
		}
	}
</style>


